import React, { useState, useEffect } from 'react';

import './index.scss';
import { ITransition } from 'src/ui/App';

interface IProps {
  transitions: ITransition[];

  activeTransition: string;
  onTransition: (transition: string) => void;
}

type Props = IProps;

const Transitions: React.SFC<Props> = ({
  transitions,
  activeTransition,
  onTransition
}) => {
  return (
    <div className="ev-transition-container">
      <button
        key="all"
        className={`ev-transition-option ${activeTransition === '' &&
          'active'}`}
        onClick={() => onTransition('')}>
        View All
      </button>
      {transitions &&
        transitions.map(transition => {
          return (
            <button
              key={transition.name}
              className={`ev-transition-option ${activeTransition ===
                transition.name && 'active'}`}
              onClick={() => onTransition(transition.name)}>
              {transition.label}
            </button>
          );
        })}
    </div>
  );
};

export default Transitions;
