export interface IKeyplan {
  floor: number;
  fileName: string;
}

export interface IOrbitReelOptions {
  units?: IUnit[];
  keyplans?: IKeyplan[];
  onUnitClicked?: (unit: IUnit) => void;
  onThetaChanged?: (theta: number) => void;
  onAmenityClicked?: (name: string) => void;
  onAmenityRegionClicked?: (region: { name: string; url: string }) => void;
  assetStoreUrl?: string;
}

export interface IUnitOverlayFields {
  disableBedrooms?: boolean;
  disableBathrooms?: boolean;
  disableSize?: boolean;
  disableAvailability?: boolean;
  disablePricing?: boolean;
  disableFeatures?: boolean;
}

export interface ICustomOptions {
  disableUnitFilterControls?: boolean;
  disableUnitOverlay?: boolean;
  disableIntroductionPopup?: boolean;
  mode?: 'all' | 'units' | 'amenities' | 'floors';
  initialFrameNumber?: number;
  disableAmenitiesGallery?: boolean;
  logoSrc?: string;
  unitOverlayFields?: IUnitOverlayFields;
  debugMode?: boolean;
}

export async function orbitReelCreateFromUrsula(
  container: HTMLDivElement,
  token: string,
  options?: IOrbitReelOptions,
  customOptions?: ICustomOptions
) {
  const buildingData = await axios.get(
    `https://ursula.adminsage.com/api/getBuildingData?token=${token}`
  );

  const buildingPlans = await axios.get(
    `https://ursula.adminsage.com/api/getUnitPlans?token=${token}`
  );
  const unitPlans = buildingPlans.data.UnitPlans;

  let processedUnits = [];

  if (buildingData.status === 200) {
    const {
      data: {
        data: { units },
      },
    } = buildingData;

    processedUnits = Object.keys(units).map((key) => {
      const unit = units[key];

      let {
        number,
        bedrooms,
        bathrooms,
        status,
        floor,
        planId,
        internalPrice,
        squareFeet,
      } = unit;
      bedrooms = Math.floor(Number(bedrooms));
      bathrooms = Math.floor(Number(bathrooms));
      floor = Number(floor);
      squareFeet = Number(squareFeet);

      const unitPlan = unitPlans.find((plan) => plan.id == planId);
      const { files, SpecialFeatures } = unitPlan;

      return {
        name: number,
        bedrooms,
        bathrooms,
        floor,
        isUnavailable: status !== 'Available',
        floorplanAssetsSrc: files,
        floorplanName: SpecialFeatures,
        price: Number(internalPrice),
        squareFeet,
      };
    });
  }

  return orbitReelFromToken(
    container,
    token,
    {
      units: processedUnits,
      ...options,
    },
    customOptions
  );
}

export async function orbitReelFromToken(
  container: HTMLDivElement,
  token: string,
  externalOptions: IOrbitReelOptions,
  customOptions?: ICustomOptions
) {
  ReactDOM.render(
    <App
      token={token}
      externalOptions={{
        ...externalOptions,
        units: externalOptions.units ? externalOptions.units : [],
      }}
      customOptions={customOptions}
    />,
    container
  );

  let isReelReady = false;
  let eventQueue = [];

  document.addEventListener('EV_EVT--REEL_READY', () => {
    isReelReady = true;

    if (eventQueue.length > 0) {
      const latestEvent = eventQueue.pop();
      document.dispatchEvent(latestEvent);
    }
  });

  return {
    overrideFilter: (filter: (unit: IUnit) => boolean) => {
      const overrideFilterEvent = new CustomEvent('EV_EVT--OVERRIDE_FILTER', {
        detail: filter,
      });

      if (!isReelReady) {
        eventQueue.push(overrideFilterEvent);
      } else {
        document.dispatchEvent(overrideFilterEvent);
      }
    },
  };
}
