import React, { useEffect, useRef, useState } from 'react';
import './index.scss';
import { IUnitOverlayFields } from 'src';

interface IProps {
  name: string;
  bedrooms: number;
  bathrooms: number;
  squareFeet: number;
  available: boolean;
  floorplanSrc: string;
  onClose: () => void;
  onExpandImage: (src: string) => void;
  fontSize: number;
  price: number;
  collapseRows: boolean;
  unitOverlayFields: IUnitOverlayFields;
}

type Props = IProps;

const CLOSE_BUTTON_B64 = `data:image/png;base64,`;

const UnitOverlay: React.SFC<Props> = ({
  name,
  bedrooms,
  bathrooms,
  squareFeet,
  available,
  onClose,
  floorplanSrc,
  onExpandImage,
  fontSize,
  price,
  collapseRows,
  unitOverlayFields
}) => {
  return (
    <div
      className={`ev-unit-overlay-container${collapseRows ? ' collapse' : ''}`}>
      <div className="ev-gallery-container">
        <img src={floorplanSrc} onClick={() => onExpandImage(floorplanSrc)} />
      </div>
      <div
        className="ev-unit-detail-container"
        style={{ fontSize: `${fontSize}px` }}>
        <h1 className="ev-unit-detail-name">Unit {name}</h1>
        {!unitOverlayFields.disableBedrooms && (
          <div className="ev-unit-detail">
            <div>Bedrooms</div>
            <span>{bedrooms}</span>
          </div>
        )}
        {!unitOverlayFields.disableBathrooms && (
          <div className="ev-unit-detail">
            <div>Bathrooms</div>
            <span>{bathrooms}</span>
          </div>
        )}
        {!unitOverlayFields.disableSize && (
          <div className="ev-unit-detail">
            <div>Size</div>
            <span>{squareFeet} sq ft</span>
          </div>
        )}

        {!unitOverlayFields.disableAvailability && (
          <div className="ev-unit-detail">
            <div>Availability</div>
            <span>{available ? 'Available Now' : 'Unavailable'}</span>
          </div>
        )}

        {!unitOverlayFields.disablePricing && (
          <div className="ev-unit-detail">
            <div>Pricing</div>
            <span>{price && price > 0 ? `$${price}` : 'Unknown'}</span>
          </div>
        )}

        {!unitOverlayFields.disableFeatures && (
          <div className="ev-unit-detail">
            <div>Features</div>
            <div className="ev-unit-features">
              <span>In-Unit Washer/Dryer</span>
              <span>Floor-To-Ceiling Windows</span>
              <span>Juliet Balcony</span>
            </div>
          </div>
        )}
      </div>
      <button
        className="ev-close-unit-overlay"
        onClick={onClose}
        style={{ fontSize: `${fontSize}px` }}>
        <img src={CLOSE_BUTTON_B64} />
      </button>
    </div>
  );
};

export default UnitOverlay;
