// Just so webpack-dev-server reloads when types.ts is changed.
const _ = dummy.x;

const pathStyle = `
  fill: #0C0E13;
  fill-opacity: 0.5;
  cursor: pointer;
  stroke-width: 1;
  stroke: #1d1d1d;
`;

const textStyle = `
user-select: none;
font-family: 'Roboto', sans-serif;
font-size: 20px;
text-anchor: middle;
dominant-baseline: middle;
fill: #dadada;
pointer-events: none;
`;

export function orbitReelCreate(
    options: OrbitReelOptions
): OrbitReelState | undefined {
    console.log("[createOrbitReel]: options: ", options);

    const state: OrbitReelState = {
        shownUnits: options.units,
        orbitImageCount: 0,
        orbitReelWidth: 0,
        orbitReelHeight: 0,
        orbitReelAspect: 0,
        anglePerImage: 0,
        imageRectangle: { x: 0, y: 0, width: 0, height: 0 },
        domElements: {
            container: undefined,
            svgElement: undefined,
            svgImageGroup: undefined,
            labelGroup: undefined,
            transitionVideoElement: undefined,
            clickableRegionsGroup: undefined,
        },
        isRenderingLabels: false,
        isRenderingFloors: false,
        imageStore: [],
        imageLowResStore: [],
        currentOrbitAngle: 0,
        currentOrbitIndex: 0,
        currentVisibleImageElement: undefined,
        currentVisibleImageIndex: 0,
        input: {
            isMouseDown: false,
            mousePosition: [0, 0],
        },
        movementSpeed: options.movementSpeed || 1,
        movementDirection: 0,
        isMoving: false,
        isUserInputLocked: false,
        moveToFrameTargetIndex: 0,
        transitionVideoEnded: false,
        occlusion: options.occlusion,
        options,
    };

    state.orbitImageCount = options.orbitImageUrls.length;

    if (360 % state.orbitImageCount || state.orbitImageCount === 0) {
        console.warn(
            `[createOrbitReel]: Invalid number of images:  ${state.orbitImageCount}`
        );

        return undefined;
    }

    state.domElements.container = options.container;
    state.anglePerImage = 360 / state.orbitImageCount;
    state.orbitReelWidth = state.domElements.container.clientWidth;
    state.orbitReelHeight = state.domElements.container.clientHeight;
    state.orbitReelAspect = state.orbitReelWidth / state.orbitReelHeight;

    state.domElements.svgElement = document.createElementNS(
        "http://www.w3.org/2000/svg",
        "svg"
    );

    state.domElements.svgElement.setAttribute(
        "xmlns",
        "http://www.w3.org/2000/svg"
    );
    state.domElements.svgElement.setAttribute(
        "xmlns:xlink",
        "http://www.w3.org/1999/xlink"
    );
    state.domElements.svgElement.setAttribute("version", "1.1");

    state.domElements.svgElement.setAttributeNS(
        "http://www.w3.org/2000/svg",
        "width",
        `${state.orbitReelWidth}`
    );
    state.domElements.svgElement.setAttributeNS(
        "http://www.w3.org/2000/svg",
        "height",
        `${state.orbitReelHeight}`
    );
    state.domElements.svgElement.style.width = `${state.orbitReelWidth}px`;
    state.domElements.svgElement.style.height = `${state.orbitReelHeight}px`;
    state.domElements.container.appendChild(state.domElements.svgElement);

    state.domElements.svgImageGroup = document.createElementNS(
        "http://www.w3.org/2000/svg",
        "g"
    );
    state.domElements.svgElement.appendChild(state.domElements.svgImageGroup);

    state.domElements.labelGroup = document.createElementNS(
        "http://www.w3.org/2000/svg",
        "g"
    );
    state.domElements.svgElement.appendChild(state.domElements.labelGroup);
    state.domElements.labelGroup.id = "svg-label-group";

    state.domElements.clickableRegionsGroup = document.createElementNS(
        "http://www.w3.org/2000/svg",
        "svg"
    );
    state.domElements.clickableRegionsGroup.setAttribute(
        "viewBox",
        "0 0 1920 1080"
    );
    state.domElements.clickableRegionsGroup.style.position = "absolute";
    state.domElements.clickableRegionsGroup.style.top = "0px";
    state.domElements.clickableRegionsGroup.style.left = "0px";
    state.domElements.clickableRegionsGroup.style.zIndex = "500";

    state.domElements.container.appendChild(
        state.domElements.clickableRegionsGroup
    );
    state.domElements.clickableRegionsGroup.id = "clickable-regions-group";
    state.domElements.clickableRegionsGroup.style.pointerEvents = "none";

    const style = document.createElement("style");
    style.type = "text/css";
    style.innerHTML =
        ".sage-reel-label { fill-opacity: 0.5; } .sage-reel-label:hover { fill-opacity: 1; } ";
    document.getElementsByTagName("head")[0].appendChild(style);

    state.imageRectangle = getFittedImageRectangle(
        options.imageWidth,
        options.imageHeight,
        options.container.clientWidth,
        options.container.clientHeight
    );

    window.addEventListener("resize", () => {
        state.imageRectangle = getFittedImageRectangle(
            options.imageWidth,
            options.imageHeight,
            options.container.clientWidth,
            options.container.clientHeight
        );

        setCurrentOrbitIndex(state, state.currentOrbitIndex, true);
    });

    console.log("[createOrbitReel]: Initial state", state);

    return state;
}

export async function orbitReelInitialize(
    orbitReel: OrbitReelState,
    frame: number = 0
) {
    if (frame > orbitReel.orbitImageCount || frame < 0) {
        throw "[orbitReelInitialize]: Invalid initialImage Index";
    }

    initOrbitReelInput(orbitReel);
    startOrbitReelLoop(orbitReel);

    orbitReel.isUserInputLocked = true;

    await loadSVGImages(
        orbitReel.options.orbitImageUrls,
        [frame],
        orbitReel.imageStore,
        orbitReel.imageRectangle,
        (image, index) => {
            orbitReel.domElements.svgImageGroup.appendChild(image);
            setCurrentOrbitIndex(orbitReel, orbitReel.currentOrbitIndex);
        }
    );
}

function IsMovementDirectionReverse(orbitReel: OrbitReelState) {
    let target = orbitReel.moveToFrameTargetIndex;
    let current = orbitReel.currentOrbitIndex;
    let count = orbitReel.orbitImageCount;
    let reverse = false;
    let delta = 0;
    let reverseDelta = 0;

    if (current < target) {
        delta = target - current;
        reverseDelta = count - target + current;
    } else {
        reverseDelta = current - target;
        delta = count - current + target;
    }

    return reverseDelta < delta;
}

function startOrbitReelLoop(orbitReel: OrbitReelState) {
    function frame() {
        if (orbitReel.isMoving) {
            if (
                orbitReel.currentOrbitIndex !== orbitReel.moveToFrameTargetIndex
            ) {
                let reverse =
                    orbitReel.moveToFrameTargetIndex <
                    orbitReel.currentOrbitIndex;

                if (IsMovementDirectionReverse(orbitReel)) {
                    orbitReelDecrementFrame(orbitReel);
                } else {
                    orbitReelIncrementFrame(orbitReel);
                }
            } else {
                orbitReel.isMoving = false;
                if (orbitReel.moveToFrameTargetCallback) {
                    orbitReel.moveToFrameTargetCallback();
                    orbitReel.moveToFrameTargetCallback = undefined;
                }
            }
        } else {
            if (orbitReel.movementDirection) {
                const theta = mod(
                    orbitReel.currentOrbitAngle -
                        orbitReel.anglePerImage *
                            orbitReel.movementSpeed *
                            orbitReel.movementDirection,
                    360
                );

                setCurrentOrbitAngle(orbitReel, theta);
            }

            orbitReel.movementDirection = 0;
        }

        if (
            orbitReel.orbitReelWidth !==
                orbitReel.domElements.container.clientWidth ||
            orbitReel.orbitReelHeight !==
                orbitReel.domElements.container.clientHeight
        ) {
            orbitReel.orbitReelWidth =
                orbitReel.domElements.container.clientWidth;
            orbitReel.orbitReelHeight =
                orbitReel.domElements.container.clientHeight;
            orbitReel.orbitReelAspect =
                orbitReel.orbitReelWidth / orbitReel.orbitReelHeight;

            orbitReel.domElements.svgElement.setAttributeNS(
                "http://www.w3.org/2000/svg",
                "width",
                `${orbitReel.orbitReelWidth}`
            );
            orbitReel.domElements.svgElement.setAttributeNS(
                "http://www.w3.org/2000/svg",
                "height",
                `${orbitReel.orbitReelHeight}`
            );
            orbitReel.domElements.svgElement.style.width = `${orbitReel.orbitReelWidth}px`;
            orbitReel.domElements.svgElement.style.height = `${orbitReel.orbitReelHeight}px`;
        }

        const image = orbitReel.imageStore[orbitReel.currentOrbitIndex];
        if (
            image &&
            image.style.width !== `${orbitReel.orbitReelWidth}` &&
            image.style.height !== `${orbitReel.orbitReelHeight}`
        ) {
            image.style.width = `${orbitReel.orbitReelWidth}`;
            image.style.height = `${orbitReel.orbitReelHeight}`;
            image.setAttribute("x", "0");
            image.setAttribute("y", "0");
            image.setAttribute(
                "viewBox",
                `0 0 ${orbitReel.orbitReelWidth} ${orbitReel.orbitReelHeight}`
            );
        }

        requestAnimationFrame(frame);
    }

    frame();
}

export async function orbitReelPreload(
    orbitReel: OrbitReelState,
    frameStep: number = 1,
    callback?: (count: number, total: number) => void
): Promise<void> {
    frameStep = Math.max(1, Math.min(orbitReel.orbitImageCount, frameStep));

    console.log("framestep = ", frameStep);

    let indices = [];
    let i = 0;
    while (i < orbitReel.orbitImageCount) {
        indices.push(i);
        i += frameStep;
    }

    const store = [];
    let preloadedImagesCounter = 0;
    let numberOfImagesToPreload = indices.length;

    await loadSVGImages(
        orbitReel.options.orbitImageUrls,
        indices,
        store,
        orbitReel.imageRectangle,
        (img, index) => {
            ++preloadedImagesCounter;
            callback &&
                callback(preloadedImagesCounter, numberOfImagesToPreload);
        }
    );
}

export async function orbitReelStart(
    orbitReel: OrbitReelState,
    initialImageIndex: number = 0
): Promise<void> {
    if (
        initialImageIndex > orbitReel.orbitImageCount ||
        initialImageIndex < 0
    ) {
        throw "[orbitReelStart]: Invalid initialImage Index";
    }

    const imageLoadingIndexSequences = getImageLoadingIndexSequences(
        orbitReel.orbitImageCount
    );

    orbitReel.isUserInputLocked = false;

    // First, load 4 low resolution images.
    await loadSVGImages(
        orbitReel.options.orbitLowResImageUrls,
        getImageIndexSequenceForDivisor(
            orbitReel.orbitImageCount,
            4,
            initialImageIndex
        ),
        orbitReel.imageLowResStore,
        orbitReel.imageRectangle,
        (image, index) => {
            orbitReel.domElements.svgImageGroup.appendChild(image);
            setCurrentOrbitIndex(orbitReel, orbitReel.currentOrbitIndex);
        }
    );

    let imageLoadedCounter = 0;
    const startTime = performance.now();
    for (let i = 0; i < imageLoadingIndexSequences.length; ++i) {
        const sequence = imageLoadingIndexSequences[i];

        await loadSVGImages(
            orbitReel.options.orbitImageUrls,
            sequence,
            orbitReel.imageStore,
            orbitReel.imageRectangle,
            (image, index) => {
                ++imageLoadedCounter;
                //    console.log(`[orbitReelStart]: ${imageLoadedCounter}/${orbitReel.orbitImageCount} (index = ${index})`);
                orbitReel.domElements.svgImageGroup.appendChild(image);
                setCurrentOrbitIndex(orbitReel, orbitReel.currentOrbitIndex);
            }
        );
    }

    const loadTime = (performance.now() - startTime) / 1000;
    console.log(
        `[orbitReelStart]: Loaded ${
            orbitReel.orbitImageCount
        } images in ${loadTime.toFixed(2)}s`
    );
}

export function orbitReelSetLabelsVisible(
    orbitReel: OrbitReelState,
    visible: boolean
) {
    orbitReel.isRenderingLabels = visible;
    orbitReel.isRenderingFloors = false;

    if (!visible) {
        const { labelGroup } = orbitReel.domElements;

        labelGroup.innerHTML = "";
    } else {
        setCurrentOrbitIndex(orbitReel, orbitReel.currentOrbitIndex, true);
    }
}

export function orbitReelSetFloorsVisible(
    orbitReel: OrbitReelState,
    visible: boolean
) {
    orbitReel.isRenderingFloors = visible;
    orbitReel.isRenderingLabels = false;

    if (!visible) {
        const { labelGroup } = orbitReel.domElements;
        labelGroup.innerHTML = "";
    } else {
        setCurrentOrbitIndex(orbitReel, orbitReel.currentOrbitIndex, true);
    }
}

export function orbitReelUpdateShownUnits(
    orbitReel: OrbitReelState,
    shownUnits: IUnit[]
) {
    orbitReel.shownUnits = shownUnits;

    setCurrentOrbitIndex(orbitReel, orbitReel.currentOrbitIndex, true);
}

export function setCurrentOrbitAngle(orbitReel: OrbitReelState, angle: number) {
    let index = GetImageIndexForAngle(orbitReel, angle);

    if (index < 0) {
        console.warn(
            `[setCurrentOrbitAngle]: ${angle} is not divisible by ${orbitReel.anglePerImage}`
        );
        return;
    }

    setCurrentOrbitIndex(orbitReel, index);
}

export function GetImageIndexForAngle(
    orbitReel: OrbitReelState,
    angle: number
) {
    const clampedAngle = mod(angle, 360);

    if (clampedAngle % orbitReel.anglePerImage) {
        return -1;
    }

    return clampedAngle / orbitReel.anglePerImage;
}

export function setCurrentOrbitIndex(
    orbitReel: OrbitReelState,
    index: number,
    ignoreImageUniqueness: boolean = false
) {
    index = mod(index, orbitReel.orbitImageCount);

    if (orbitReel.options.thetaChangedCallback) {
        orbitReel.options.thetaChangedCallback(index * orbitReel.anglePerImage);
    }

    orbitReel.currentOrbitIndex = index;
    orbitReel.currentOrbitAngle = index * orbitReel.anglePerImage;

    const newImageElement =
        orbitReel.imageStore[index] || orbitReel.imageLowResStore[index];

    if (!newImageElement) {
        console.warn(`[,]: Invalid image index ${index}.`);
        return;
    }

    if (
        newImageElement === orbitReel.currentVisibleImageElement &&
        !ignoreImageUniqueness
    ) {
        return;
    }

    if (orbitReel.currentVisibleImageElement) {
        orbitReel.currentVisibleImageElement.style.visibility = "hidden";
    }

    newImageElement.style.visibility = "visible";

    orbitReel.currentVisibleImageElement = newImageElement;
    orbitReel.currentVisibleImageIndex = index;

    const { camera, labels } = orbitReel.options;
    const { isRenderingLabels, isRenderingFloors } = orbitReel;

    if (camera && labels && isRenderingLabels) {
        const { camera: threeJsCamera, target } = computeCamera(
            camera,
            1920 / 1080,
            index
        );
        let visibleLabels: OrbitReelLabelData[] = [];

        labels.forEach((label) => {
            if (orbitReel.options.debugMode) {
                visibleLabels.push(label);
            } else if (
                orbitReel.shownUnits &&
                orbitReel.shownUnits.map((u) => u.name).includes(label.name)
            ) {
                visibleLabels.push(label);
            }

            if (orbitReel.occlusion) {
                visibleLabels = visibleLabels.filter((label) =>
                    orbitReel.occlusion[orbitReel.currentOrbitIndex].includes(
                        label.id
                    )
                );
            }
        });
        const svgLabels = computeSVGLabels(
            threeJsCamera,
            target,
            visibleLabels,
            orbitReel.imageRectangle
        );

        console.log("labels:", svgLabels);

        renderLabels(orbitReel, svgLabels);
    } else if (camera && labels && isRenderingFloors) {
        const { camera: threeJsCamera, target } = computeCamera(
            camera,
            1920 / 1080,
            index
        );

        let visibleLabels: OrbitReelLabelData[] = labels;
        labels.forEach((label) => {
            if (orbitReel.occlusion) {
                visibleLabels = visibleLabels.filter((label) =>
                    orbitReel.occlusion[orbitReel.currentOrbitIndex].includes(
                        label.id
                    )
                );
            }
        });

        const svgLabels = computeSVGLabels(
            threeJsCamera,
            target,
            visibleLabels,
            orbitReel.imageRectangle
        );

        renderFloors(orbitReel, svgLabels);
    }
}

function renderLabels(
    orbitReel: OrbitReelState,
    svgLabels: ComputedLabelData[]
) {
    const { labelGroup } = orbitReel.domElements;

    labelGroup.innerHTML = "";
    const pathsByName: { [name: string]: SVGPathElement[] } = {};

    for (let i = 0; i < svgLabels.length; i++) {
        const label = svgLabels[i];

        if (!label) continue;

        const g = document.createElementNS("http://www.w3.org/2000/svg", "g");
        const {
            labelClickedCallback,
            labelEnterCallback,
            labelLeaveCallback,
        } = orbitReel.options;

        const unitToCheck = orbitReel.shownUnits.find(
            (u) => u.name === label.name
        );
        const isUnavailable = unitToCheck ? unitToCheck.isUnavailable : true;

        label.instructions.forEach((instruction) => {
            const path = document.createElementNS(
                "http://www.w3.org/2000/svg",
                "path"
            );
            path.setAttribute("d", instruction);
            path.style.cssText = pathStyle;
            path.setAttribute("class", "sage-reel-label");

            labelClickedCallback &&
                path.addEventListener("mousedown", () => {
                    labelClickedCallback(label);
                });

            const baseColor = isUnavailable ? "rgb(222, 14, 19)" : "#0c690e";

            path.style.fill = baseColor;

            path.addEventListener("mouseenter", () => {
                pathsByName[label.name].forEach((path) => {
                    path.style.fill = isUnavailable
                        ? "rgb(222, 14, 19)"
                        : "#14ba19";
                });

                labelEnterCallback && labelEnterCallback(label);
            });
            path.addEventListener("mouseleave", () => {
                pathsByName[label.name].forEach((path) => {
                    path.style.fill = baseColor;
                });

                labelLeaveCallback && labelLeaveCallback(label);
            });

            pathsByName[label.name] = (pathsByName[label.name] || []).concat([
                path,
            ]);

            g.appendChild(path);
        });

        const text = document.createElementNS(
            "http://www.w3.org/2000/svg",
            "text"
        );
        text.setAttribute("x", label.x.toString());
        text.setAttribute("y", label.y.toString());
        text.style.cssText = textStyle;
        text.style.fill = isUnavailable ? "rgba(170, 170, 170, 1)" : "White";

        text.textContent = label.name;

        if (label.textVisible) {
            g.appendChild(text);
        }

        labelGroup.appendChild(g);
    }
}

function renderFloors(
    orbitReel: OrbitReelState,
    svgLabels: ComputedLabelData[]
) {
    const { labelGroup } = orbitReel.domElements;

    labelGroup.innerHTML = "";
    const pathsByFloor: { [floor: number]: SVGPathElement[] } = {};

    for (let i = 0; i < svgLabels.length; i++) {
        const label = svgLabels[i];

        if (!label) continue;

        const unit = orbitReel.shownUnits.find((u) => u.name === label.name);

        const g = document.createElementNS("http://www.w3.org/2000/svg", "g");
        const { floorClickedCallback } = orbitReel.options;
        label.instructions.forEach((instruction) => {
            const path = document.createElementNS(
                "http://www.w3.org/2000/svg",
                "path"
            );
            path.setAttribute("d", instruction);
            path.style.cssText = pathStyle;
            path.setAttribute("class", "sage-reel-floor-label");

            floorClickedCallback &&
                path.addEventListener("mousedown", () => {
                    floorClickedCallback(unit.floor);
                });

            const baseColor = "rgb(12, 14, 19)";

            path.style.fill = baseColor;

            path.addEventListener("mouseenter", () => {
                pathsByFloor[unit.floor].forEach((path) => {
                    path.style.fill = "#14ba19";
                });
            });
            path.addEventListener("mouseleave", () => {
                pathsByFloor[unit.floor].forEach((path) => {
                    path.style.fill = baseColor;
                });
            });

            pathsByFloor[unit.floor] = (pathsByFloor[unit.floor] || []).concat([
                path,
            ]);

            g.appendChild(path);
        });

        labelGroup.appendChild(g);
    }
}

export function tryGetImageElementForIndex(
    orbitReel: OrbitReelState,
    index: number
) {
    let _index = mod(index, orbitReel.orbitImageCount);

    return orbitReel.imageStore[_index] || orbitReel.imageLowResStore[_index];
}

export function seekNextFrame(
    orbitReel: OrbitReelState,
    reverse: boolean = false
) {
    let currentIndex = orbitReel.currentOrbitIndex;
    let inc = reverse ? -1 : 1;

    for (let i = 0; i < orbitReel.orbitImageCount; i++) {
        currentIndex = mod(currentIndex + inc, orbitReel.orbitImageCount);

        let element = tryGetImageElementForIndex(orbitReel, currentIndex);

        if (element) {
            setCurrentOrbitIndex(orbitReel, currentIndex);
            return;
        }
    }
}

export function orbitReelMoveToFrame(
    orbitReel: OrbitReelState,
    index: number
): Promise<void> {
    return new Promise(function (resolve, reject) {
        orbitReel.isMoving = true;
        orbitReel.moveToFrameTargetIndex = mod(
            index,
            orbitReel.orbitImageCount
        );
        orbitReel.moveToFrameTargetCallback = () => {
            resolve();
        };
    });
}

export function orbitReelMoveToAngle(
    orbitReel: OrbitReelState,
    angle: number
): Promise<void> {
    let index = GetImageIndexForAngle(orbitReel, angle);

    if (index < 0) {
        return;
    }

    return orbitReelMoveToFrame(orbitReel, index);
}

export function orbitReelIncrementFrame(orbitReel: OrbitReelState) {
    setCurrentOrbitIndex(orbitReel, orbitReel.currentOrbitIndex + 1);
}

export function orbitReelDecrementFrame(orbitReel: OrbitReelState) {
    setCurrentOrbitIndex(orbitReel, orbitReel.currentOrbitIndex - 1);
}

function initOrbitReelInput(orbitReel: OrbitReelState): void {
    const { container } = orbitReel.domElements;

    window.addEventListener("keydown", (e) => {
        if (orbitReel.isUserInputLocked) {
            return;
        }

        if (e.key === "ArrowRight") {
            seekNextFrame(orbitReel);
        }

        if (e.key === "ArrowLeft") {
            seekNextFrame(orbitReel, true);
        }

        if (e.key === "g") {
            // orbitReelMoveToFrame(orbitReel, 60);
            orbitReelMoveToAngle(orbitReel, 90);
        }
    });

    container.addEventListener("mousedown", (e) => {
        orbitReel.input.isMouseDown = true;
        orbitReel.input.mousePosition = [e.clientX, e.clientY];
    });

    container.addEventListener("touchstart", (e) => {
        orbitReel.input.isMouseDown = true;
        orbitReel.input.mousePosition = [
            e.touches[0].clientX,
            e.touches[0].clientY,
        ];
    });

    container.addEventListener("mouseup", (e) => {
        orbitReel.input.isMouseDown = false;
    });

    container.addEventListener("touchend", (e) => {
        orbitReel.input.isMouseDown = false;
    });

    container.addEventListener("mouseleave", (e) => {
        orbitReel.input.isMouseDown = false;
    });

    container.addEventListener("touchcancel", (e) => {
        orbitReel.input.isMouseDown = false;
    });

    container.addEventListener("mousemove", (e) => {
        if (
            !orbitReel.input.isMouseDown ||
            orbitReel.isUserInputLocked ||
            !orbitReel.domElements.svgElement.contains(e.target as any)
        )
            return;

        const displacementX = e.clientX - orbitReel.input.mousePosition[0];

        if (Math.abs(displacementX)) {
            orbitReel.movementSpeed = Math.max(
                1,
                Math.floor(Math.abs(displacementX) / 20)
            );

            orbitReel.movementDirection = Math.sign(displacementX);
        }

        orbitReel.input.mousePosition = [e.clientX, e.clientY];
    });

    container.addEventListener("touchmove", (e) => {
        if (
            !orbitReel.input.isMouseDown ||
            orbitReel.isUserInputLocked ||
            !orbitReel.domElements.svgElement.contains(e.target as any)
        )
            return;

        const displacementX =
            e.touches[0].clientX - orbitReel.input.mousePosition[0];

        if (Math.abs(displacementX)) {
            orbitReel.movementSpeed = Math.max(
                1,
                Math.floor(Math.abs(displacementX) / 20)
            );

            orbitReel.movementDirection = Math.sign(displacementX);
        }

        orbitReel.input.mousePosition = [
            e.touches[0].clientX,
            e.touches[0].clientY,
        ];
    });
}

function orbitReelGetTransitionByName(orbitReel: OrbitReelState, name: string) {
    const { transitions } = orbitReel.options;

    if (!transitions) {
        return undefined;
    }

    for (let i = 0; i < transitions.length; i++) {
        if (transitions[i].name === name) {
            return transitions[i];
        }
    }

    return undefined;
}

export function orbitReelExitTransition(
    orbitReel: OrbitReelState
): Promise<void> {
    if (
        !orbitReel.activeTransition ||
        !orbitReel.domElements.transitionVideoElement ||
        !orbitReel.transitionVideoEnded
    ) {
        return;
    }

    const {
        options: { amenityModeCallback },
    } = orbitReel;

    amenityModeCallback && amenityModeCallback(true);

    const video = orbitReel.domElements.transitionVideoElement;

    video.setAttribute("src", orbitReel.activeTransition.videoReverseSource);

    orbitReel.domElements.clickableRegionsGroup.innerHTML = "";
    orbitReel.domElements.clickableRegionsGroup.style.pointerEvents = "none";

    if (orbitReel.activeTransition.posterImageSource) {
        video.setAttribute(
            "poster",
            orbitReel.activeTransition.posterImageSource
        );
    }

    return new Promise(function (resolve, reject) {
        const onVideoEnded = () => {
            orbitReel.domElements.container.removeChild(video);
            video.removeEventListener("ended", onVideoEnded);
            orbitReel.isUserInputLocked = false;
            orbitReel.transitionVideoEnded = false;
            orbitReel.activeTransition = undefined;

            resolve();
        };

        video.addEventListener("ended", onVideoEnded);
        video.play();
    });
}

export async function orbitReelActivateTransition(
    orbitReel: OrbitReelState,
    transitionIndexOrName: number | string,
    onTransitionVideoEnded?: () => void
) {
    let transition: OrbitReelTransitionData;

    if (orbitReel.activeTransition) {
        if (orbitReel.transitionVideoEnded) {
            await orbitReelExitTransition(orbitReel);
            onTransitionVideoEnded();
        } else {
            console.log(
                "[orbitReelActivateTransition]: Another transition is currently active; exiting."
            );
            return;
        }
    }

    if (typeof transitionIndexOrName === "number") {
        transition = orbitReel.options.transitions[transitionIndexOrName];
    } else {
        transition = orbitReelGetTransitionByName(
            orbitReel,
            transitionIndexOrName
        );
    }

    orbitReel.options.amenityClickedCallback &&
        transition &&
        orbitReel.options.amenityClickedCallback(transition.name);
    if (!transition) {
        console.warn(
            `[orbitReelActivateTransition]: Invalid transition: ${transitionIndexOrName}.`
        );
        return;
    }

    const video = document.createElement("video");
    const source = document.createElement("source");
    orbitReel.domElements.container.appendChild(video);
    source.setAttribute("src", transition.videoSource);

    let top =
        -orbitReel.domElements.svgElement.clientHeight +
        orbitReel.imageRectangle.y;
    let left = orbitReel.imageRectangle.x;

    let width = orbitReel.imageRectangle.width;

    video.style.position = "absolute";

    //I don't know what this is for.

    // video.style.top = `${top}px`;
    // video.style.left = `${left}px`;
    // video.style.width = `${width}px`;

    video.style.width = `${orbitReel.orbitReelWidth}px`;
    video.style.height = `${orbitReel.orbitReelHeight}px`;
    video.style.top = "0px";
    video.style.left = "0px";

    video.style.objectFit = "fill";

    orbitReel.isUserInputLocked = true;
    orbitReel.activeTransition = transition;
    orbitReel.domElements.transitionVideoElement = video;

    if (transition.posterImageSource) {
        const img = new Image();
        img.src = transition.posterImageSource;
    }

    const onVideoEnded = () => {
        orbitReel.transitionVideoEnded = true;
        video.removeEventListener("ended", onVideoEnded);
        onTransitionVideoEnded && onTransitionVideoEnded();

        const {
            options: { amenityModeCallback },
        } = orbitReel;

        amenityModeCallback && amenityModeCallback(true);

        if (
            transition.clickableRegions &&
            transition.clickableRegions.length > 0
        ) {
            orbitReel.domElements.clickableRegionsGroup.style.pointerEvents =
                "all";

            transition.clickableRegions.forEach(
                ({ name, url, instructions }) => {
                    const path: SVGPathElement = document.createElementNS(
                        "http://www.w3.org/2000/svg",
                        "path"
                    );
                    path.setAttribute("d", instructions);
                    path.setAttribute("class", "ev-clickable-region");

                    const title: SVGTitleElement = document.createElementNS(
                        "http://www.w3.org/2000/svg",

                        "title"
                    );
                    title.textContent = name;

                    path.appendChild(title);

                    path.onclick = () => {
                        orbitReel.options.amenityRegionClickedCallback &&
                            orbitReel.options.amenityRegionClickedCallback({
                                name,
                                url,
                            });
                        window.open(url);
                    };

                    orbitReel.domElements.clickableRegionsGroup.appendChild(
                        path
                    );
                }
            );
        }
    };

    orbitReelMoveToAngle(orbitReel, transition.angle).then(() => {
        video.appendChild(source);
        video.play();
        video.addEventListener("ended", onVideoEnded);
    });
}
