import React, { useState, useEffect } from 'react';

import { IUnit } from '../../../orbit-reel/types';

import './index.scss';

interface IProps {
  onFilterChange: (filter: (unit: IUnit) => boolean) => void;
}

type Props = IProps;

enum BedsBathFilter {
  ONE = '1',
  TWO = '2',
  THREE = '3',
  FOUR_PLUS = '4+',
  ALL = 'All'
}

const FilterOptions = [
  BedsBathFilter.ONE,
  BedsBathFilter.TWO,
  BedsBathFilter.THREE,
  BedsBathFilter.FOUR_PLUS,
  BedsBathFilter.ALL
];

const UnitSearch: React.SFC<Props> = ({ onFilterChange }) => {
  const [bedrooms, setBedrooms] = useState<BedsBathFilter[]>([
    BedsBathFilter.ALL
  ]);
  const [bathrooms, setBathrooms] = useState<BedsBathFilter[]>([
    BedsBathFilter.ALL
  ]);
  const [unitQuery, setUnitQuery] = useState<string>('');

  useEffect(() => {
    const unitFilter = (unit: IUnit): boolean => {
      const matchesBedsBaths = (
        bedsBaths: number,
        choices: BedsBathFilter[]
      ): boolean => {
        const targetBedsBaths = `${Math.floor(bedsBaths)}`;

        for (const option of FilterOptions) {
          if (targetBedsBaths === option && choices.includes(option)) {
            return true;
          }
        }

        if (bedsBaths >= 4 && choices.includes(BedsBathFilter.FOUR_PLUS)) {
          return true;
        }

        return choices.includes(BedsBathFilter.ALL);
      };

      return (
        matchesBedsBaths(unit.bedrooms, bedrooms) &&
        matchesBedsBaths(unit.bathrooms, bathrooms) &&
        unit.name.toLowerCase().includes(unitQuery.toLowerCase())
      );
    };

    onFilterChange(unitFilter);
  }, [bedrooms, bathrooms, unitQuery]);

  const updateBedsBaths = (
    option: BedsBathFilter,
    current: BedsBathFilter[]
  ): BedsBathFilter[] => {
    let updated = [...current];

    updated = updated.filter(o => o !== BedsBathFilter.ALL);

    if (updated.includes(option)) {
      updated = updated.filter(o => o !== option);
    } else {
      updated.push(option);
    }

    if (updated.length === 0) {
      updated.push(BedsBathFilter.ALL);
    }

    if (updated.includes(BedsBathFilter.ALL)) {
      updated = [BedsBathFilter.ALL];
    }

    return updated;
  };

  const updateBathrooms = (option: BedsBathFilter) => {
    setBathrooms(updateBedsBaths(option, bathrooms));
  };

  const updateBedrooms = (option: BedsBathFilter) => {
    setBedrooms(updateBedsBaths(option, bedrooms));
  };

  const updateUnitQuery = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const {
      target: { value }
    } = evt;

    setUnitQuery(value);
  };

  return (
    <div className="ev-search-container">
      <div className="ev-search-group">
        <span className="ev-search-group-title">Bedrooms</span>
        <div className="ev-search-row">
          {FilterOptions.map(option => {
            return (
              <button
                className={`ev-search-option ${bedrooms.includes(option) &&
                  'active'}`}
                onClick={() => updateBedrooms(option)}>
                {option}
              </button>
            );
          })}
        </div>
      </div>
      <div className="ev-search-group">
        <span className="ev-search-group-title">Bathrooms</span>
        <div className="ev-search-row">
          {FilterOptions.map(option => {
            return (
              <button
                className={`ev-search-option ${bathrooms.includes(option) &&
                  'active'}`}
                onClick={() => updateBathrooms(option)}>
                {option}
              </button>
            );
          })}
        </div>
      </div>
      <div className="ev-search-group">
        <span className="ev-search-group-title">Search By Unit Name</span>
        <input
          className="ev-search-input"
          type="text"
          value={unitQuery}
          onChange={updateUnitQuery}
        />
      </div>
    </div>
  );
};

export default UnitSearch;
