import {
    ComputedLabelData,
    Rectangle,
    OrbitReelCameraData,
    OrbitReelLabelData,
    Vector3Data,
    PartialLabel,
} from "./types";
import * as THREE from "three";
import * as Bowser from "bowser";

const browser = Bowser.getParser(window.navigator.userAgent);
