export function formatInteger(integer: number, digits: number = 3) {
    return `00${integer.toString()}`.slice(-digits);
}

export function getDivisors(num: number): number[] {
    const result: number[] = [];

    for (let i = 1; i <= num; i++) {
        if (num % i === 0) {
            result.push(i);
        }
    }

    return result;
}

export function mod(x: number, n: number) {
    return ((x % n) + n) % n;
}

export function getFittedImageRectangle(
    imageWidth: number,
    imageHeight: number,
    containerWidth: number,
    containerHeight: number
): Rectangle {
    let width = 0;
    let height = 0;
    let x = 0;
    let y = 0;

    let imageAspect = imageWidth / imageHeight;
    let containerAspect = containerWidth / containerHeight;

    if (containerAspect < imageAspect) {
        width = containerWidth;
        height = width / imageAspect;
        x = 0;
        y = containerHeight / 2 - height / 2;
    } else {
        height = containerHeight;
        width = height * imageAspect;
        x = containerWidth / 2 - width / 2;
        y = 0;
    }

    return {
        x,
        y,
        width,
        height,
    };
}

export function createSVGImage(rect: Rectangle): SVGImageElement {
    const image = document.createElementNS(
        "http://www.w3.org/2000/svg",
        "image"
    );
    image.setAttribute("preserveAspectRatio", "none");
    image.setAttribute("x", rect.x.toString());
    image.setAttribute("y", rect.y.toString());
    image.setAttribute("viewBox", `0 0 ${rect.width} ${rect.height}`);
    image.setAttribute("width", `100%`);
    image.setAttribute("height", `100%`);

    image.style.visibility = "hidden";
    image.style.width = `${rect.width}px`;
    image.style.height = `${rect.height}px`;

    return image;
}

export async function loadSVGImages(
    imageUrls: string[],
    indices: number[],
    store: SVGImageElement[],
    imageRectangle: Rectangle,
    callback?: (image: SVGImageElement, index: number) => void
) {
    return new Promise(function (resolve, reject) {
        let imageLoadedCounter = 0;
        const imageCount = indices.length;

        if (imageCount === 0) {
            resolve();
        }

        function onImageLoaded(image: SVGImageElement, index: number): void {
            ++imageLoadedCounter;

            if (!store[index] && image) {
                store[index] = image;

                if (callback) {
                    callback(image, index);
                }
            }

            if (imageLoadedCounter >= imageCount) {
                resolve();
            }
        }

        for (let i = 0; i < imageCount; i++) {
            const index = indices[i];
            const imageUrl = imageUrls[index];

            if (store[index]) {
                onImageLoaded(store[index], index);
                continue;
            }

            const image = createSVGImage(imageRectangle);

            if (browser.isBrowser("safari")) {
                const proxy = new Image();

                proxy.addEventListener("load", () => {
                    image.setAttributeNS(
                        "http://www.w3.org/1999/xlink",
                        "href",
                        imageUrl
                    );

                    onImageLoaded(image, index);
                });

                proxy.addEventListener("error", () => {
                    console.warn(`Failed to load image: ${imageUrl}, ${index}`);
                    onImageLoaded(undefined, index);
                });

                proxy.src = imageUrl;
            } else {
                image.addEventListener("error", () => {
                    console.warn(`Failed to load image: ${imageUrl}, ${index}`);
                    onImageLoaded(undefined, index);
                });

                image.addEventListener("load", () => {
                    onImageLoaded(image, index);
                });
                image.setAttributeNS(
                    "http://www.w3.org/1999/xlink",
                    "href",
                    imageUrl
                );
            }
        }
    });
}

export function computeCamera(
    camera: OrbitReelCameraData,
    aspectRatio: number,
    index: number
): {
    camera: THREE.PerspectiveCamera;
    target: THREE.Vector3;
} {
    const _camera = new THREE.PerspectiveCamera(
        camera.fov,
        aspectRatio,
        0.1,
        100000
    );

    const px = camera.frames[index][0];
    const py = camera.frames[index][1];
    const pz = camera.frames[index][2];

    _camera.position.x = px;
    _camera.position.y = pz;
    _camera.position.z = -py;

    const target = new THREE.Vector3(
        camera.target[0],
        camera.target[2],
        -camera.target[1]
    );

    _camera.lookAt(target);

    _camera.updateMatrix();
    _camera.updateMatrixWorld();
    _camera.updateProjectionMatrix();
    _camera.matrixWorldInverse.getInverse(_camera.matrixWorld);
    // _camera.target = target;

    return { camera: _camera, target };
}

function toScreenXY(
    worldPosition: THREE.Vector3,
    camera: THREE.PerspectiveCamera,
    width: number,
    height: number,
    offsetX: number,
    offsetY: number
): THREE.Vector2 {
    const position = worldPosition.clone();

    const widthHalf = width / 2;
    const heightHalf = height / 2;

    const projectedPosition = position.project(camera);

    const left = projectedPosition.x * widthHalf + widthHalf + offsetX;
    const top = -(projectedPosition.y * heightHalf) + heightHalf + offsetY;

    return new THREE.Vector2(left, top);
}

export function vector3DataToVector3(v: Vector3Data): THREE.Vector3 {
    return new THREE.Vector3(v.x, v.y, v.z);
}

function compileLabel(label: PartialLabel): ComputedLabelData {
    const instructions: string[] = [];

    label.rectangles.forEach(({ tl, tr, bl, br }) => {
        let instruction = `M ${tl.x} ${tl.y} `;
        instruction += `L ${tr.x} ${tr.y} `;
        instruction += `L ${br.x} ${br.y} `;
        instruction += `L ${bl.x} ${bl.y} Z`;
        instructions.push(instruction);
    });

    return {
        instructions,
        name: label.name,
        id: label.id,
        distance: label.minDistance,
        x: label.screenSpaceTotal.x / label.screenSpaceAreaTotal,
        y: label.screenSpaceTotal.y / label.screenSpaceAreaTotal,
        textVisible: label.textVisible,
    };
}

export function computeSVGLabels(
    camera: THREE.PerspectiveCamera,
    target: THREE.Vector3,
    labels: OrbitReelLabelData[],
    imageRectangle: Rectangle
): ComputedLabelData[] {
    const byName: { [name: string]: OrbitReelLabelData[] } = {};
    labels.forEach((label) => {
        byName[label.name] = (byName[label.name] || []).concat([label]);
    });

    const groupedLabels = Object.keys(byName).map((name) => {
        return byName[name];
    });

    const svgLabels = [];

    groupedLabels.forEach((labels) => {
        let partialLabel: PartialLabel = {
            minDistance: Number.MAX_VALUE,
            rectangles: [],
            screenSpaceTotal: new THREE.Vector2(),
            name: "",
            id: -1,
            textVisible: false,
            screenSpaceAreaTotal: 0,
        };

        labels.forEach((label) => {
            const normal = vector3DataToVector3(label.normal);
            const center = vector3DataToVector3(label.position);

            const cameraDirection = camera.position
                .clone()
                .sub(center)
                .normalize();
            const dot = normal.dot(cameraDirection);

            const theta = Math.acos(dot) * 57.2958;
            const visible = dot >= 0;
            const textVisible =
                partialLabel.textVisible || (dot >= 0 && theta <= 75);
            partialLabel.textVisible = textVisible;
            if (visible) {
                const projected: THREE.Vector2[] = [];
                label.vertices.forEach((v) => {
                    const value = vector3DataToVector3(v);
                    projected.push(
                        toScreenXY(
                            value,
                            camera,
                            imageRectangle.width,
                            imageRectangle.height,
                            imageRectangle.x,
                            imageRectangle.y
                        )
                    );
                });

                const tl = projected[0];
                const tr = projected[1];
                const br = projected[2];
                const bl = projected[3];

                partialLabel.rectangles.push({
                    tl,
                    tr,
                    bl,
                    br,
                });

                //tl    tr

                //bl     br

                let _br = tl.clone();
                let _bl = bl.clone();
                let _tr = tr.clone();
                let _tl = br.clone();

                const lhsHeight = _bl.clone().sub(_tl).y;
                const lhsBase = _br.clone().sub(_bl).x;

                const rhsHeight = _br.clone().sub(_tr).y;
                const rhsBase = _tr.clone().sub(_tl).x;

                const screenSpaceArea =
                    Math.max(1, (lhsHeight * lhsBase) / 2) +
                    Math.max(1, (rhsHeight * rhsBase) / 2);

                const positionScreenSpace = toScreenXY(
                    center,
                    camera,
                    imageRectangle.width,
                    imageRectangle.height,
                    imageRectangle.x,
                    imageRectangle.y
                );

                partialLabel.screenSpaceTotal.add(
                    positionScreenSpace.multiplyScalar(screenSpaceArea)
                );
                partialLabel.screenSpaceAreaTotal += screenSpaceArea;

                partialLabel.name = label.name;
                partialLabel.id = label.id;
                const { x, y, z } = label.position;
                partialLabel.minDistance = Math.min(
                    partialLabel.minDistance,
                    camera.position.distanceTo(new THREE.Vector3(x, y, z))
                );
            }
        });

        if (partialLabel && partialLabel.rectangles.length > 0) {
            svgLabels.push(compileLabel(partialLabel));
        }
    });

    svgLabels.sort((a, b) => b.distance - a.distance);

    return svgLabels;
}
